"use client";
import { useEffect, useRef, useState } from 'react';
import { World } from '../engine/World';

export default function SimulationCanvas() {
    const canvasRef = useRef<HTMLCanvasElement>(null);
    const containerRef = useRef<HTMLDivElement>(null);
    const worldRef = useRef<World | null>(null);
    const [stats, setStats] = useState({ pop: 0, food: 0, avgSpeed: 0 });

    useEffect(() => {
        if (!canvasRef.current || !containerRef.current) return;

        // Resize canvas to fit container
        const width = containerRef.current.clientWidth;
        const height = 600;
        canvasRef.current.width = width;
        canvasRef.current.height = height;

        const w = new World(width, height);
        worldRef.current = w;

        let animationId: number;
        const ctx = canvasRef.current.getContext('2d');

        let frameCount = 0;

        const render = () => {
            if (!ctx) return;
            w.update();

            // Clear
            ctx.fillStyle = '#111';
            ctx.fillRect(0, 0, w.width, w.height);

            // Draw Food
            ctx.fillStyle = '#4ade80'; // Green-400
            for (const f of w.food) {
                ctx.beginPath();
                ctx.arc(f.x, f.y, 4, 0, Math.PI * 2);
                ctx.fill();
            }

            // Draw Entities
            for (const e of w.entities) {
                ctx.fillStyle = e.genome.color;
                ctx.beginPath();
                ctx.arc(e.position.x, e.position.y, e.size, 0, Math.PI * 2);
                ctx.fill();

                // Draw eye/direction
                const eyeX = e.position.x + e.velocity.normalize().x * (e.size * 0.7);
                const eyeY = e.position.y + e.velocity.normalize().y * (e.size * 0.7);
                ctx.fillStyle = 'white';
                ctx.beginPath();
                ctx.arc(eyeX, eyeY, e.size * 0.3, 0, Math.PI * 2);
                ctx.fill();
            }

            // Update stats every 60 frames
            frameCount++;
            if (frameCount % 30 === 0) {
                const totalSpeed = w.entities.reduce((acc, e) => acc + e.maxSpeed, 0);
                setStats({
                    pop: w.entities.length,
                    food: w.food.length,
                    avgSpeed: w.entities.length ? totalSpeed / w.entities.length : 0
                });
            }

            animationId = requestAnimationFrame(render);
        };

        render();

        return () => cancelAnimationFrame(animationId);
    }, []);

    return (
        <div ref={containerRef} className="w-full flex flex-col gap-4">
            <div className="flex gap-4 p-4 bg-zinc-900 rounded-lg text-white font-mono text-sm">
                <div>Population: {stats.pop}</div>
                <div>Food: {stats.food}</div>
                <div>Avg Speed: {stats.avgSpeed.toFixed(2)}</div>
            </div>
            <canvas ref={canvasRef} className="border border-zinc-700 rounded-lg shadow-2xl" />
        </div>
    );
}
